<script>
	import service from '../../service.js';
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				btn_kg:0,
				data_list:[],
				catelist:[],
				type:0,
				page:1,
				size:20
			}
		},
		computed:{
			...mapState([
				'hasLogin',
				'loginMsg',
				'wxlogin'
			])
		},
		/**
		 * 生命周期函数--监听页面加载
		 */
		onLoad: function (options) {
			this.getCate()
			this.onRetry()
		},
		
		/**
		 * 生命周期函数--监听页面初次渲染完成
		 */
		onReady: function () {
		
		},
		
		/**
		 * 生命周期函数--监听页面显示
		 */
		onShow: function () {
		
		},
		
		/**
		 * 生命周期函数--监听页面隐藏
		 */
		onHide: function () {
		
		},
		
		/**
		 * 生命周期函数--监听页面卸载
		 */
		onUnload: function () {
		
		},
		
		/**
		 * 页面相关事件处理函数--监听用户下拉动作
		 */
		onPullDownRefresh: function () {
		 this.onRetry()
		},
		
		/**
		 * 页面上拉触底事件的处理函数
		 */
		onReachBottom: function () {
		this.getdata()
		},
		
		/**
		 * 用户点击右上角分享
		 */
		onShareAppMessage: function () {
		
		},
		methods: {
			onRetry(){
				this.page=1
				this.getdata()
			},
			getCate(){
				var that =this
				var datas={
					type:3
				}
				// 单个请求
				service.P_get('/cate/list',datas).then(res => {
				  console.log(res)
					if(res.code==1){
						that.catelist=res.data
						
					}
				}).catch(e => {
				  console.log(e)
					uni.showToast({
						icon:'none',
						title:'获取数据失败'
					})
				})
				return
				// // 一个页面多个请求
				/*Promise.all([
				  service.P_get('/cate/list',datas),
				  service.P_get('/cate/list',datas)
				]).then(result => {
				  console.log(result)
				}).catch(e => {
				  console.log(e)
				})*/
			},
			getdata(){
				let that = this
				console.log(that.btn_kg)
				if(that.btn_kg==1){
					return
				}else{
					that.btn_kg=1
				}
				var datas={
					token: that.loginMsg.userToken,
					c_id:that.type,
					page:that.page,
					size:that.size
	
				}
				service.P_get('/baseAdvocacy/lst',datas).then(res => {
				  console.log(res)
					that.btn_kg=0
					
					if(res.code==1){
						var datas = res.data
						if (datas.length == 0) {
							if(that.page>1){
								uni.showToast({
									icon: 'none',
									title: '暂无更多数据'
								})
							}
							
							that.btn_kg=0
							return
						}
						if(that.page==1){
							that.data_list =datas
						}else{
							
							that.data_list = that.data_list.concat(datas)
						}
						that.btn_kg=0
						that.page++
					}
				}).catch(e => {
				  console.log(e)
					that.btn_kg=0
					uni.showToast({
						icon:'none',
						title:'获取数据失败'
					})
				})
			},
			bindcur(e){
				var that =this
			  console.log(e.currentTarget.dataset.type)
			  that.type= e.currentTarget.dataset.type
				// that.getdata()
				that.onRetry()
			},
			jump(e){
			  service.jump(e)
			},
			
		}
	}
</script>

